<template>
<a
  class="button"
  :href="path"
>
  <span class="icon is-small" v-if="icon.length > 0">
    <i :class="'fa ' + icon"></i>
  </span>
  <span class="text is-hidden-touch">
    {{ text }}
  </span>
</a>
</template>

<script>
export default {
  name: 'button-href-link',
  props: {
    text: {
      default: 'button',
      type: String
    },
    path: {
      default: '',
      type: String
    },
    icon: {
      default: '',
      type: String
    }
  }
}
</script>

<style scoped>
</style>



